import React, {useEffect, useState} from 'react';
import {
  FlatList,
  SafeAreaView,
  StatusBar,
  StyleSheet,
  Text,
  View,
} from 'react-native';
import DropDownPicker from 'react-native-dropdown-picker';
import FastImage from 'react-native-fast-image';

function HomeScreen() {
  const [open, setOpen] = useState(false);
  const [value, setValue] = useState([
    'italy',
    'spain',
    'barcelona',
    'finland',
  ]);
  const [items, setItems] = useState([
    {label: 'Spain', value: 'spain'},
    {label: 'Madrid', value: 'madrid', parent: 'spain'},
    {label: 'Barcelona', value: 'barcelona', parent: 'spain'},

    {label: 'Italy', value: 'italy'},
    {label: 'Rome', value: 'rome', parent: 'italy'},

    {label: 'Finland', value: 'finland'},
  ]);
  return (
    <SafeAreaView style={styles.background}>
      <StatusBar />
      <View style={styles.background}>
        <Text style={{alignSelf: 'center', padding: 10, fontSize: 22}}>
          This is version 2.1
        </Text>
        <Text style={{alignSelf: 'center', padding: 10, fontSize: 22}}>
          {'Demo shared lib main js (lib dropdown)'}
        </Text>
        <FastImage
          source={require('./assets/images.png')}
          style={styles.repackImg}
          resizeMethod={'auto'}
        />
        <DropDownPicker
          open={open}
          value={value}
          items={items}
          setOpen={setOpen}
          setValue={setValue}
          setItems={setItems}
          theme="DARK"
          multiple={true}
          mode="BADGE"
          badgeDotColors={[
            '#e76f51',
            '#00b4d8',
            '#e9c46a',
            '#e76f51',
            '#8ac926',
            '#00b4d8',
            '#e9c46a',
          ]}
        />
      </View>
    </SafeAreaView>
  );
}

export default HomeScreen;

const styles = StyleSheet.create({
  hiLabel: {
    color: 'black',
    fontSize: 28,
    fontWeight: '600',
    margin: 20,
  },
  item: {
    height: 100,
    flex: 1,
    padding: 10,
    margin: 10,
    borderRadius: 5,
    alignItems: 'center',
    justifyContent: 'center',
    borderColor: 'black',
    backgroundColor: '#EEE',
  },
  background: {
    flex: 1,
    backgroundColor: 'white',
  },
  repackImg: {
    width: '60%',
    aspectRatio: 1,
    alignSelf: 'center',
  },
});
